.Login {
    font-family: sans-serif;
    -ms-flex-align: center;
        align-items: center;
    
    margin: 90px 0;
  }
  
  h1 {
    text-align: center;
  }
  
  .form {
    max-width: 500px;
    width: 100%;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
   margin: 10px auto;
   -webkit-box-sizing: border-box;
           box-sizing: border-box;
   -ms-flex-line-pack: center;
       align-content: center;
    
  }
  
  label,
  input {
    display: block;
    max-width: 300px;
    width: 100%;
  }
  
  label {
    margin-bottom: 5px;
    height: 22px;
  }
  
  input {
    
    padding: 10px;
    border-radius: 3px;
    margin: auto;
    margin-bottom: 20px;
    border: 1px solid #777;
   
  }
  
  input.error {
    border-color: red;
  }
  
  .input-feedback {
    color: rgb(235, 54, 54);
    margin-top: -15px;
    font-size: 14px;
    margin-bottom: 20px;
  }
  
  .button {
    padding: 10px 15px;
    margin: auto;
    width:200px;
    background-color: rgb(70, 153, 179);
    color: white;
    -ms-flex-align: center;
        align-items: center;
    border: 1px solid rgb(70, 153, 179);
    background-color: 250ms;
  }
  
  .button:hover {
    cursor: pointer;
    background-color: white;
    color: rgb(70, 153, 179);
  }
  .autherror{
    color: red;
    
  }
  


